<template>
    <div class="index_app">
        <!-- 顶部导航栏 -->
        <div class="index_menu">
            <div class="index_menu_content">
                <ul class="index_menu_ul">
                     <li style="margin-right:10px">
                        欢迎来到购车网：
                    </li>
                    <li>
                        <a href="javascript:;" style="color:red" @click="toLogin">{{loginList}}</a>
                    </li>
                    <li> 
                        <a href="javascript:;" @click="toregister">免费注册</a> 
                    </li>
                    <li>                     
                        <a href="javascript:;" @click="toCart" v-show="mygwc">我的购物车</a> 
                    </li>
                    <li class="Out">                    
                        <a href="javascript:;" v-show="out"  @click="clear_session">退出</a> 
                    </li>
                </ul>
            </div>    
        </div>
        <!-- 搜索栏 -->
        <div class="index_title">
            <img src="../assets/car_logo.png" alt="">
            <div class="Search">
               <van-search @keydown.13="onSearch" @input="onlabel" @focus="onfocus" @blur="onblur" v-model="value" shape="round" background="#3c322d"  show-action placeholder="请输入搜索关键词" >
                    <template #action>
                        <div @click="onSearch" class="Search_font" >搜索</div>
                    </template>
                </van-search>   
                <div class="Search_label" v-show="showLabel">
                    <ul>
                        <li v-for="(item,i) of Search_label" :key="i" @mousedown="onSearch_label(item.lid)">{{item.title}}</li>
                    </ul>
                </div> 
            </div>        
        </div>  
        
         <!-- 轮播图 -->
        <div class="car">
            <el-carousel >
                <el-carousel-item v-for="(item,i) in imgArray" :key="i">
                    <img class="Map_img" :src="item" alt="">
            </el-carousel-item>
            </el-carousel>
        </div>


        <!-- 按价格搜索框 -->
        <div class="hr">
            <div class="hr2_div">
                <input type="text" v-model="min_money" placeholder="￥"> 
                -
                <input type="text" v-model="max_money" placeholder="￥">
                 <van-button class="hr2_div_bottom" color="#FF4200"  size="mini"   type="danger" @click="Search_money">确 定</van-button>  
            </div>
        </div>

        <!-- 商品列表 -->
        <div class="index_body">
            <ul class="index_body_ul" @click="clickimg">
                <li v-for="(item,i) of list" :key="i">
                   <img :src="baseURL+item.pic" class="index_body_img" :data-lid="item.lid">
                   <div class="index_body_title">
                      <span>{{item.title}}</span>
                   </div>
                   <div class="index_body_price">
                       <span >￥{{item.price.toFixed(2)}}</span>
                   </div>
                   <div class="index_button">
                     <van-button @click="addcart" :data-pic="item.pic" :data-lid="item.lid" :data-title="item.title" :data-price="item.price" size="large"   type="info">加入购物车</van-button>  
                   </div>
                </li>
            </ul>
        </div>

        <!-- 分页栏 -->
        <div class="page">
            <van-pagination v-model="pno" :page-count="pageCount" mode="simple" @change="MOM" />
        </div>

        <!-- 底部条 -->
        <div class="hr hr_button">
        </div>
    </div>
</template>
<script>
import '../css/index_gwc.css'
export default {
    data(){
        return{
            list:[],  //列表内容
            pno:1,    //页码
            pageCount:'',  //总页数
            value: '',    //搜索栏内容
            showLabel:false,  //搜索栏下label的显示状态
            Search_label:[], //搜索到的数据列表
            loginList:'',  //顶部用户名名称
            out:false,     //退出按钮的显示状态
            min_money:'',  //按价格搜索框内容1
            max_money:'',  //按价格搜索框内容2
            mygwc:false,  //我的购物车显示状态
            mom:'',  //判断分页的change事件 执行哪个按钮
            imgArray:[  //轮播图图片
                require("../assets/C5.jpg"),
                require("../assets/C0.jpg"),
                require("../assets/C1.jpg"),
                require("../assets/C2.jpg"),
                require("../assets/C3.jpg"),
                require("../assets/C4.jpg"),
            ],
            baseURL:this.global.baseURL,//引入路由
        }
    },
    created(){
        this.loadMore();  
        this.onlabel();
        this.showuanme();
    },
    methods:{
        //分页change按钮下的选中
        MOM(){
            if(this.mom==1){
                this.loadMore();
            }else if(this.mom==2){
                this. onSearch()
            }else if(this.mom==3){
                this.Search_money();
            }
        },
        //利用事件委托实现子元素的单击事件
        clickimg(e){
            //只允许img元素触发事件
            if(e.target.nodeName=="IMG"){ //判断，点击IMG元素触发事件
                var lid=e.target.dataset.lid;
                this.$router.push('/gwc/ProductInformation?lid='+lid);//路由传参
            }
        },
        //按价格搜索商品
        Search_money(){
            //给mom赋值为3
            this.mom=3;
            //
            var obj={min_money:this.min_money,max_money:this.max_money,pno:this.pno};
            this.$axios.get('/api/Search_money',{params:obj}).then(res=>{
                if(res.data.code==1){
                    this.list=res.data.rows;
                    this.pageCount = res.data.pageCount;
                    this.pno=res.data.pno
                }else{
                    this.list='';
                    this.min_money=null;
                    this.max_money=null;
                    this.$alert("未找到对应的商品！","提示信息")
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //退出按钮（清除用户记录 清除session记录）
        clear_session(){
            this.$confirm("退出登录，是否继续？","提示",{
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(()=>{
                this.$axios.get('/api/out').then(res=>{
                    if(res.data.code==1){
                        this.showuanme(); //退出成功后-重新调用一下用户显示名字
                        this.loadMore();
                    }
                }).catch(err=>{
                    console.log(err);
                })
            })
        },
        //跳转到购物车
        toCart(){
            this.$router.push('/gwc/Cart')
        },
        //请登录按钮
        toLogin(){
            if(this.loginList=="亲，请登录"){
                this.$router.push('/gwc/login')
            }
        },
        //顶部用户名显示
        showuanme(){
            this.$axios.get('/api/showUname').then(res=>{
                if(res.data.code==1){
                    this.loginList=res.data.uname
                    this.out=true;  //退出按钮显示
                    this.mygwc=true;//购物车显示
                    console.log("uname="+res.data.uname);
                }else{
                     this.loginList=res.data.uname
                     this.out=false; //退出按钮隐藏
                     this.mygwc=false; //购物车隐藏
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //搜索标签的显示与隐藏
        onblur(){
            this.showLabel=false;
         },
        onfocus(){
             this.showLabel=true;
        },
        //label表下单独li搜索
        onSearch_label(lid){
            var obj={lid:lid};
            this.$axios.get('/api/Search_label',{params:obj}).then(res=>{
                if(res.data.code==1){
                    this.min_money=null;
                    this.max_money=null;
                    this.pno=1;
                    this.pageCount=1;
                    this.list=res.data.rows;
                }else{
                    this.list='';
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //搜索框下的label表内容
        onlabel(){
            var obj={key:this.value}
            console.log(this.value)
            this.$axios.get('/api/Search',{params:obj}).then(res=>{
                if(res.data.code==1){
                 this.Search_label=res.data.rows;
                 }else{
                    this.list='';
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //搜索按钮-搜索商品
        onSearch(){
            if(this.value==''){
                this.loadMore();
                return;
            }
            //给mom赋值2
            this.mom=2;
            //
            var obj={key:this.value,pno:this.pno}
            console.log(this.value)
            this.$axios.get('/api/Search',{params:obj}).then(res=>{
                if(res.data.code==1){
                    this.list=res.data.rows;
                    this.Search_label=res.data.rows;
                    this.pageCount = res.data.pageCount;
                    this.pno=res.data.pno;
                    this.min_money=null;  //钱 输入框清空
                    this.max_money=null;
                }else{
                    this.list='';
                    this.pno=1;
                    this.pageCount=1;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //添加购物车
        addcart(e){
            //e.target代替this 使用在目标处理函数 
            //dataset.属性  来获取属性值
        var lid=e.target.dataset.lid;
        var pic=e.target.dataset.pic;
        var title=e.target.dataset.title;
        var price=e.target.dataset.price;
        // 发送ajax
        var obj={lid:lid,pic:pic,title:title,price:price}
        this.$axios.get('/api/addcart',{params:obj}).then(res=>{
            if(res.data.code==-1){
                this.$alert("请登录后重试！","提示信息");
            }else{
                this.$alert("成功加入购物车！","提示信息");
            }
        }).catch(err=>{
            console.log(err)
        })
        },

        //用户注册
        toregister(){
            this.$router.push('/gwc/Register')
        },

        //商品列表
        loadMore(){
            //给mom赋值1
            this.mom=1;
            //创建参数对象
            var obj={pno:this.pno};
            //发送ajax请求
            this.$axios.get('/api/product',{params:obj}).then(res=>{
                //保存服务器返回数据
                //将赋值改拼错
                //this.list = this.list.concat(res.data.rows);
                this.list = res.data.rows;
                this.pageCount = res.data.pageCount;
            }).catch(err=>{
                console.log(err);
            })
        document.documentElement.scrollTop = 0 //加载页面后滚动到顶部
        }
    }

}
</script>